const TodoItem = ({
  todo = {
    done: true,
    title: 'Business meeting',
    time: '3 hours',
    text: 'Meeting to discuss ideas'
  }
}) => {
  return(
      <li>
        <input type="checkbox" defaultChecked={todo.done}/>
        {todo.title}
        ({todo.time})
        {todo.text}
      </li>
  );
}
export default TodoItem;